import React from "react";
import { renderFieldInputForPost } from "../Common/units/renderField";
import { maxLength, minValue, required } from "../Common/units/validation";
import {  Navigate } from "react-router-dom";
import { Form, Field } from 'react-final-form'

const LoginForm=(props)=>{
    let maxLength20=maxLength(20)
    let maxLength40=maxLength(40)
    const composeValidators = (...validators) => value =>
  validators.reduce((error, validator) => error || validator(value), undefined)
  
    return <Form
    onSubmit={props.onSubmit}
    
    >
        {({handleSubmit})=>(
        <form onSubmit={handleSubmit}>
            <div>
                <Field validate={composeValidators(required, maxLength40)} placeholder="email" component={renderFieldInputForPost} type="email" name="email"/>
            </div>
            <div>
                <Field validate={composeValidators(required, maxLength20)} placeholder="password" component={renderFieldInputForPost} type="password" name="password"/>
            </div>
            <div>
                <Field  component={"input"} type="checkbox" name="rememberMe"/> <span>remember me</span>
            </div>
            <div >{props.error
            ?<>
            <div>{props.error.error}</div><CaptchViewComp {...props}/>
            </>
            :""}</div>
            
            <div>
                <button>Submit</button>
            </div>
        </form>
    )
        }
    </Form>
}

const CaptchViewComp=({url,...props})=>{
    
    return <div>
                <div><img src={url} alt="captcha"/></div>
                <Field validate={required} placeholder="write letters above" component={renderFieldInputForPost} type="text" name="captcha"/>
    </div>
}

const Auth=(props)=>{
    
    if (props.isAuth) {
        return <Navigate to={"/profile"}/>
    }
    return <div>
        <h1>LOGIN</h1>
        <LoginForm {...props} onSubmit={props.onSubmit}/>
    </div>
}
export default Auth